.advantages--background {
  background: $color-ghost-white;
}

.advantages__wrapper {
  padding: 120px 0;
  margin: 0 auto;
  max-width: #{"min(76%, 1038px)"};

  @media (max-width: $vp-1199) {
    padding: 100px 0;
    max-width: #{"min(88.2%, 1105px)"};
  }

  @media (max-width: $vp-767) {
    padding: 70px 0;
    max-width: 100%;
  }
}

.advantages__wrapper h2 {
  margin: 0;
  margin-bottom: 80px;
  font-family: "Bebas Neue", sans-serif;
  font-weight: 700;
  font-size: 49px;
  text-align: center;
  text-transform: uppercase;
  color: $color-sapphire;

  @media (max-width: $vp-1199) {
    margin-bottom: 60px;
    font-size: 42px;
  }

  @media (max-width: $vp-767) {
    margin-bottom: 60px;
    font-size: 32px;
  }
}

.advantages__list {
  padding: 0;
  margin: 0;
  display: grid;
  // grid-template-columns: #{"min(32.8%, 340px)"} #{"min(23.2%, 240px)"} #{"min(35.2%, 366px)"};
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-between;
  list-style: none;

  @media (max-width: $vp-1199) {
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    row-gap: 40px;
  }

  @media (max-width: $vp-767) {
    display: flex;
    flex-direction: column;
  }
}

.advantages__item {
  display: grid;
  grid-template-columns: 0.4fr 1fr;

  &:last-child,
  &:nth-last-child(-n + 2) {
    align-items: flex-end;
    border-top: 2px solid $color-crimson-tarnsparent;

    @media (max-width: $vp-1199) {
      border-top: none;
    }
  }

  @media (max-width: $vp-767) {
    grid-template-columns: 1fr;
  }
}

.advantages__description {
  grid-column: 2 / 3;

  @media (max-width: $vp-767) {
    // justify-self: center;
    margin: auto;
    max-width: #{"min(78%, 600px)"};
    grid-column: 1 / 2;
    grid-row: 2 / 3;
  }
}

.advantages__description h3 {
  margin: 0;
  margin-bottom: 10px;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 150%;
  color: $color-sapphire;

  @media (max-width: $vp-1199) {
    margin-bottom: 5px;
    font-size: 20px;
  }

  @media (max-width: $vp-767) {
    text-align: center;
  }
}

.advantages__description p {
  margin: 0;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 140%;
  color: $color-sapphire;

  @media (max-width: $vp-1199) {
  }

  @media (max-width: $vp-767) {
    text-align: center;
  }
}

.advantages__item span {
  margin: 0;
  margin-right: 30px;
  display: flex;
  flex-direction: column;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  font-size: 71px;
  line-height: 100%;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  background-image: $cobalt-to-top;

  & span {
    margin-left: 0;
    font-family: "Roboto Condensed", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: $color-persian-blue;
    background-image: none;

    @media (max-width: $vp-767) {
      margin: 0 auto;
    }
  }

  @media (max-width: $vp-1199) {
    margin-right: 20px;
    font-size: 58px;
  }

  @media (max-width: $vp-767) {
    margin: 0 auto;
    margin-bottom: 10px;
  }
}

.advantages__item--differ {
  padding: 53px 0;
  max-width: 240px;
  grid-column: 2 / 3;
  grid-row: 1 / 3;
  display: grid;
  justify-self: center;
  background: $color-resolution-blue;

  @media (max-width: $vp-1199) {
    padding: 35px 0;
    min-width: 442px;
    grid-column: 1 / 3;
    grid-row: 2 / 3;
  }

  @media (max-width: $vp-767) {
    padding: 30px 0;
    min-width: 100%;
  }
}

.advantages__item--differ .advantages__description {
  grid-column: 1 / 3;
  grid-row: 2 / 3;
}

.advantages__item--differ p {
  margin: 0 auto;
  max-width: #{"min(71.6%, 172px)"};
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  text-align: center;
  color: $color-default-white;

}

.advantages__item--differ span {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-column: 1 / 3;
  grid-row: 1 / 2;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  font-size: 74px;
  line-height: 120%;
  text-align: center;
  color: $color-default-white;

  & span {
    margin: 0;
    margin-bottom: 25px;
    font-size: 26px;
    line-height: 150%;
    color: $color-default-white;

    @media (max-width: $vp-1199) {
      margin-bottom: 15px;
      font-size: 22px;
    }
  }

  @media (max-width: $vp-1199) {
    margin-right: auto;
    line-height: 100%;
    // margin-bottom: 30px;
  }

  @media (max-width: $vp-767) {
    margin-bottom: 25px;
  }
}
